<template>
  <div id="alls" class="player-container">
    <div class="player">
      <video-player class="video-player vjs-custom-skin"
                    ref="videoPlayer"
                    :playsinline="false"
                    :options="playerOptions"
                    @play="onPlay($event)"
                    @pause="OnPause($event)"
                    @statechanged="playerStateChanged($event)"
                    >
      </video-player>
    </div>
  </div>
</template>

<script>
  import { videoPlayer } from 'vue-video-player'
  import '../../node_modules/video.js/dist/video-js.css'
  import '../../node_modules/vue-video-player/src/custom-theme.css'

  export default {
    props: {
      videoUrl: String,
      state: Boolean,
    },
    data() {
      return {
        playerOptions: {
          // playbackRates: [0.7, 1.0, 1.5, 2.0], //
          autoplay: false,
          muted: false,
          loop: false,
          preload: 'auto',
          language: 'zh-CN',
          aspectRatio: '16:9',
          fluid: true,
          sources: [{
            type: 'video/mp4',
            src: this.videoUrl  // m3u8
            // src: "/file/lessonMaterial/download?id=319"
          }],
          // poster: 'https://surmon-china.github.io/vue-quill-editor/static/images/surmon-3.jpg', // 
          width: document.documentElement.clientWidth,
          notSupportedMessage: ''
        },

      };
    },
    watch: {
      videoUrl: function (val) {
        if (val !== "") {
          this.$refs.videoPlayer.player.dispose();
          this.$refs.videoPlayer.player.src(val);
        }
      },
      //  state
      state: function (val) {
        if (val) {
          this.$refs.videoPlayer.player.pause();
        }
      },
    },
    components: {
      videoPlayer
    },
    mounted () {
      console.log("player mounted");
    },
    methods: {
      onPlay (player) {
        console.log("play...");
      },
      OnPause (player) {
        console.log("pause...");
      },
      playerStateChanged (player) {
        console.log("player state changed...");
      }
    },
    computed: {
      player () {
        return this.$refs.videoPlayer.player;
      }
    }
  }
</script>
<style scoped>
  #alls {
    width: 100%;
    height: 50%;
    /*background-color: #0e38b1;*/
  }

  .player-container {
    /*background-color: #efefef;*/
    min-height: 100%;
  }
</style>



